.StatusBars {
    display: flex;
    gap: 2px;
}

.Bar {
    position: relative;
    width: 5px;
    min-height: 20px;
    border-radius: 5px;
    background-color: rgb(160, 160, 160);
}

.Popup {
    display: none;
    flex-direction: column;
    position: absolute;
    left: calc(50% - 0.5px);
    top: calc(100% + 15px);
    transform: translateX(-50%);
    z-index: 10;
    padding: 5px 10px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.1);
    border: 1px solid rgb(230, 230, 230);
}

.Bar:hover .Popup, .Popup:hover {
    display: flex !important;
}

.Popup::after {
    content: '';
    position: absolute;
    top: -11px;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    z-index: -1;
    width: 20px;
    height: 20px;
    background-color: white;
    border-top: 1px solid rgb(230, 230, 230);
    border-left: 1px solid rgb(230, 230, 230);
}

.Date {
    color: rgb(120, 120, 120);
    font-size: 14px;
    font-weight: 500;
    text-align: center;
}

.Status {
    font-weight: 500;
    font-size: 14px;
}

.Bar:not(:first-child) .Popup {
    display: none;
}