html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  --text-1: #000;

  --bg-1: #fff;
  --bg-2: #eee;
}

.lvl-green {
    background-color: #04c147 !important;
    border-color: #03b341 !important;
}

.lvl-blue {
    background-color: #3498db !important;;
    border-color: #2a8aca !important;
}

.lvl-orange {
    background-color: #ffad4f !important;;
    border-color: #f0a752 !important;
}

.lvl-red {
    background-color: #ff4242 !important;;
    border-color: #f04040 !important;
}

.lvl-txt-green {
    color: #04c147;
}

.lvl-txt-blue {
    color: #3498db;
}

.lvl-txt-orange {
    color: #ffad4f;
}

.lvl-txt-red {
    color: #ff4242;
}