.Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 100%;
    height: 100%;
    flex: 1 1 auto;
    background-color: var(--bg-2);
    overflow-y: scroll;
    padding: 20px 0px;
}

.Container a {
    text-decoration: none;
    color: #009dff;
}

.Content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1000px;
    gap: 14px;
}