.GlobalStatus {
    display: flex;
    gap: 10px;
    align-items: center;
    border-radius: 10px;
    padding: 0px 15px;
    border: 2px solid;
    margin: 0px 10px;
}

.GlobalStatus h2 {
    font-size: 16px;
    color: #fff;
}

.GlobalStatus svg {
    fill: #fff;
    height: 24px;
    aspect-ratio: 1;
}