.ServiceCard {
    display: flex;
    flex-direction: column;
}

.Row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0px;
    flex-wrap: wrap;
    cursor: pointer;
}

.ServiceCard:not(:last-child) {
    border-bottom: 1px solid rgba(120, 120, 120, 0.3);
}

.Service {
    display: flex;
    height: 100%;
}

.Service h4 {
    margin: 0;
    font-weight: 400;
    color: rgb(60, 60, 60);
    user-select: none;
}

.Service svg {
    height: 24px;
    aspect-ratio: 1;
    fill: rgb(80, 80, 80);
    transition: 0.2s;
}

.Service svg.Open {
    transform: rotate(90deg);
}

.Status {
    display: flex;
    justify-content: right;
    align-items: center;
    gap: 20px;
    height: 24px;
}

.Uptime {
    width: 70px;
    height: fit-content;
    text-align: right;
    font-size: 14px;
}

.StatusText {
    width: 130px;
    height: fit-content;
    font-size: 14px;
    font-weight: 500;
}

.FullCard {
    width: 100%;
    max-height: 0px;
    overflow: hidden;
}

.FullCard.Open {
    max-height: unset;
    border-left: 4px solid gray;
}

@media only screen and (max-width: 750px) {
    .Bars {
        width: 10px;
    }
}